@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:400,500&subset=korean);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", 'Noto Sans KR',"Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #282c34;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10vh;
}
#input {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  font-size: 2rem;
  justify-content: center;
  margin-top: 1rem;
  background-color: white;
  padding-top:0.5rem;
  padding-bottom:0.5rem;
  color: #282c34;
  border: 1rem;
  opacity: 0.8;
  outline: none;
  text-align: center;
}

#convert {
  height: 50vh;
  font-size: 2rem;
  justify-content: center;
  margin-top: 1rem;
}

#result {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 3rem;
  font-size: 2rem;
  justify-content: center;
  padding-top: 3rem;
}

label{
  margin-bottom:0.7rem;
}

.radio-tile-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  
}
.input {
  position: relative;
  height: 4rem;
  width: 4rem;
  margin: 0.5rem;
}
.radio-tile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-size: 2rem;
  color: rgba(255, 255, 255, 0.8);
  border: 0px solid rgba(255, 255, 255, 0.8);
  padding: 1rem;
  font-weight: 400;
  -webkit-transition: -webkit-transform 300ms ease;
  transition: -webkit-transform 300ms ease;
  transition: transform 300ms ease;
  transition: transform 300ms ease, -webkit-transform 300ms ease;
}

.radio-button:checked + .radio-tile {
  background-color: rgba(255, 255, 255, 0.8);
  border: 0px;
  color: #282c34;
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
  font-size: 2.2rem;
}
.radio-button {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  margin: 0;
  cursor: pointer;
}

body {
  font-family: "Noto Sans KR";
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-link {
  color: #61dafb;
}
